/* ë©”ì¸ ë°°ê²½ ì´ë¯¸ì§€ */
.main_image {
  position: relative;
  margin: auto;
  display: block;
}
  @keyframes slideInLeft {
    from {
      transform: translateX(100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
  .main_image.animate {
    animation: slideInLeft 1s ease-out forwards;
  }
    
